import React, { Component } from 'react';
import { Link, NavLink } from 'react-router-dom';
import cardBackground from './cardBackground.jpg';
import pixelart from './pixelart.jpg';

class Header extends Component {
  state = {
    ımages: [
      {
        bodyImg: pixelart
      }
    ],

    addbodyImg: {
      bodyImg: ''
    }
  };

  changebodyImg = () => {
    this.setState({
      addbodyImg: {
        ...this.state.addbodyImg,
        bodyImg: cardBackground
      }
    });
  };

  render() {
    return (
      <div className='header'>
        <div className='headerItem'>
          <NavLink exact to='/'>
            HOME|
          </NavLink>
        </div>
        <div className='headerItem'>
          <NavLink to='/about'>ABOUT|</NavLink>
        </div>
        <div className='headerItem'>
          <NavLink to='/work'>PROJECTS|</NavLink>
        </div>
        <div className='headerItem'>
          <NavLink onClick={this.changebodyImg} to='/BLOG'>
            BLOG|
          </NavLink>
        </div>
        <div className='headerItem'>
          <NavLink to='/contact'>CONTACT</NavLink>
        </div>
      </div>
    );
  }
}

export default Header;
